:root {
  --background: #ffffff;
  --foreground: #171717;
  --bg-banner: url('/assets/img/bg-banner.png');
  --bg-budget: url('/assets/img/budget.png');
}
@media (prefers-color-scheme: dark) {
  :root {
    --background: #0a0a0a;
    --foreground: #ededed;
  }
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: var(--font-inter), Inter, sans-serif;
}
html {
  scroll-behavior: smooth;
}
::selection {
  background-color: rgba(100, 103, 242, 0.3);
  color: #ffffff;
}
::-webkit-scrollbar {
  width: 8px;
}
::-webkit-scrollbar-track {
  background: #080A0C;
}
::-webkit-scrollbar-thumb {
  background: #272C35;
  border-radius: 4px;
}
::-webkit-scrollbar-thumb:hover {
  background: #6467F2;
}
* {
  scrollbar-width: thin;
  scrollbar-color: #272C35 #080A0C;
}
@layer utilities {
  .bg-banner {
    background-image: var(--bg-banner);
  }
  .bg-budget {
    background-image: var(--bg-budget);
  }
  .perspective-1000 {
    perspective: 1000px;
  }
  .perspective-500 {
    perspective: 500px;
  }
  .transform-style-3d {
    transform-style: preserve-3d;
  }
  .backface-hidden {
    backface-visibility: hidden;
  }
  .will-change-transform {
    will-change: transform;
  }
  .will-change-opacity {
    will-change: opacity;
  }
  .text-gradient {
    background: linear-gradient(135deg, #6467F2 0%, #9f7aea 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }
  .glow-blue {
    box-shadow: 0 0 20px rgba(100, 103, 242, 0.4),
                0 0 40px rgba(100, 103, 242, 0.2),
                0 0 60px rgba(100, 103, 242, 0.1);
  }
  .glow-blue-sm {
    box-shadow: 0 0 10px rgba(100, 103, 242, 0.3);
  }
  .text-glow {
    text-shadow: 0 0 20px rgba(100, 103, 242, 0.5);
  }
  .delay-100 {
    animation-delay: 100ms;
  }
  .delay-200 {
    animation-delay: 200ms;
  }
  .delay-300 {
    animation-delay: 300ms;
  }
  .delay-500 {
    animation-delay: 500ms;
  }
  .delay-700 {
    animation-delay: 700ms;
  }
  .delay-1000 {
    animation-delay: 1000ms;
  }
}
@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-15px);
  }
}
@keyframes pulse-glow {
  0%, 100% {
    box-shadow: 0 0 20px rgba(100, 103, 242, 0.4);
  }
  50% {
    box-shadow: 0 0 40px rgba(100, 103, 242, 0.6);
  }
}
@keyframes shimmer {
  0% {
    background-position: -200% 0;
  }
  100% {
    background-position: 200% 0;
  }
}
@keyframes fade-in-up {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes scale-in {
  from {
    opacity: 0;
    transform: scale(0.9);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
@keyframes slide-in-left {
  from {
    opacity: 0;
    transform: translateX(-50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes slide-in-right {
  from {
    opacity: 0;
    transform: translateX(50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes rotate-in {
  from {
    opacity: 0;
    transform: rotate(-10deg) scale(0.9);
  }
  to {
    opacity: 1;
    transform: rotate(0) scale(1);
  }
}
@keyframes border-glow {
  0%, 100% {
    border-color: rgba(100, 103, 242, 0.3);
  }
  50% {
    border-color: rgba(100, 103, 242, 0.6);
  }
}
@keyframes scroll-wheel {
  0% {
    opacity: 1;
    transform: translateY(0);
  }
  100% {
    opacity: 0;
    transform: translateY(8px);
  }
}
@keyframes bounce-slow {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(4px);
  }
}
@keyframes shine {
  0% {
    background-position: 200% 50%;
  }
  100% {
    background-position: -200% 50%;
  }
}
.animate-shine {
  animation: shine 3s linear infinite;
}
@layer utilities {
  .animate-float {
    animation: float 3s ease-in-out infinite;
  }
  .animate-pulse-glow {
    animation: pulse-glow 2s ease-in-out infinite;
  }
  .animate-shimmer {
    background: linear-gradient(
      90deg,
      transparent 0%,
      rgba(100, 103, 242, 0.1) 50%,
      transparent 100%
    );
    background-size: 200% 100%;
    animation: shimmer 2s infinite;
  }
  .animate-fade-in-up {
    animation: fade-in-up 0.6s ease-out forwards;
  }
  .animate-scale-in {
    animation: scale-in 0.5s ease-out forwards;
  }
  .animate-slide-in-left {
    animation: slide-in-left 0.6s ease-out forwards;
  }
  .animate-slide-in-right {
    animation: slide-in-right 0.6s ease-out forwards;
  }
  .animate-rotate-in {
    animation: rotate-in 0.5s ease-out forwards;
  }
  .animate-border-glow {
    animation: border-glow 2s ease-in-out infinite;
  }
  .animate-scroll-wheel {
    animation: scroll-wheel 1.5s ease-in-out infinite;
  }
  .animate-bounce-slow {
    animation: bounce-slow 1.5s ease-in-out infinite;
  }
}
@layer utilities {
  .hover-lift {
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  .hover-lift:hover {
    transform: translateY(-5px);
    box-shadow: 0 10px 30px rgba(100, 103, 242, 0.15);
  }
  .hover-glow {
    transition: box-shadow 0.3s ease;
  }
  .hover-glow:hover {
    box-shadow: 0 0 30px rgba(100, 103, 242, 0.3);
  }
  .hover-scale {
    transition: transform 0.3s ease;
  }
  .hover-scale:hover {
    transform: scale(1.05);
  }
}
input:focus,
textarea:focus {
  outline: none;
  border-color: #6467F2 !important;
  box-shadow: 0 0 0 3px rgba(100, 103, 242, 0.2);
}
button:active {
  transform: scale(0.98);
}
.link-underline {
  position: relative;
}
.link-underline::after {
  content: '';
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 0;
  height: 2px;
  background: linear-gradient(90deg, #6467F2, #9f7aea);
  transition: width 0.3s ease;
}
.link-underline:hover::after {
  width: 100%;
}
.glass {
  background: rgba(17, 19, 23, 0.7);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(39, 44, 53, 0.5);
}
.glass-dark {
  background: rgba(8, 10, 12, 0.8);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(39, 44, 53, 0.3);
}
